p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}
.container {
  margin-top: 35px;
  width: 350px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 15px;
  padding: 30px;

  background-color: #65428c;
  text-align: center;
}

.text {
  margin-bottom: 20px;
  color: aliceblue;
  font-family: 'Times New Roman', Times, serif;
  font-size: 35px;
}
